@import "/src/styles/varaibles.scss";

.card {
	@apply xl:grid-cols-3 xl:h-auto w-48 h-auto mb-2.5 p-3 duration-1000 cursor-pointer text-center relative bg-amber-200 hover:scale-90 shadow-xl md:grid-cols-2 sm:grid-cols-1 sm:h-auto;

	@apply line-clamp-2 text-ellipsis;

	.title {
		@apply text-sm mb-5 font-semibold line-clamp-1;
		margin: 10px 0;
	}

	.author {
		@apply text-sm mb-2.5 font-semibold line-clamp-1 inset-x-2.5;
	}

	img {
		@apply w-full;
		max-height: 250px;
		aspect-ratio: 1 / 2;
	}
}